.gallery {
  position: relative;
  top: -46px;
  margin-bottom: 94px;
}

.card-set {
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  margin-bottom: 80px;

  @media screen and (min-width: $mobile) and (max-width: $max-mobile) {
    margin-top: -39px;
    margin-left: -14px;
  }

  @media screen and (min-width: $tablet) and (max-width: $max-tablet) {
    margin-top: -58px;
    margin-left: -27px;
  }

  @media screen and (min-width: $desktop) {
    margin-top: -63px;
    margin-left: -70px;
  }
}

.item-event {
  position: relative;
  top: 0;
  left: 0;

  @media screen and (min-width: $mobile) and (max-width: $max-mobile) {
    margin-top: 39px;
    margin-left: 14px;
    flex-basis: calc(100% / 2 - 14px);
  }

  @media screen and (min-width: $tablet) and (max-width: $max-tablet) {
    margin-top: 58px;
    margin-left: 27px;
    flex-basis: calc(100% / 3 - 27px);
  }

  @media screen and (min-width: $desktop) {
    margin-top: 63px;
    margin-left: 70px;
    flex-basis: calc(100% / 4 - 70px);
  }

  &::before {
    content: '';
    display: inline-block;
    position: absolute;
    border: 1px solid rgba(220, 86, 197, 0.3);
    border-radius: 50px 0px;

    @media screen and (min-width: $mobile) {
      width: 110px;
      height: 100px;
      top: -10px;
      right: -10px;
    }

    @media screen and (min-width: $tablet) {
      width: 153px;
      height: 143px;
      top: -14px;
      right: -14px;
    }
  }
}

.card {
  text-align: center;
  border-radius: 50px 0 0 0;
}

.card-thumb {
  overflow: hidden;
  border-radius: 50px 0px;
}

.card-image {
  overflow: hidden;
  @media screen and (min-width: $mobile) {
    width: 128px;
    height: 161px;
    object-fit: cover;
  }

  @media screen and (min-width: $tablet) {
    width: 180px;
    height: 227px;
    object-fit: cover;
  }
}

.event-info-thumb {
  padding-top: 5px;
  font-family: $font-montserrat;

  @media screen and (min-width: $mobile) {
    .event-name {
      @include font-data(14px, $title-text-color, 700, 1.2);
    }

    .event-local-date {
      @include font-data(14px, $subtitle-text-color, 400, 1.2);
    }

    .location-link {
      @include font-data(12px, $subtitle-text-color, 600, 1.25);
    }
  }

  @media screen and (min-width: $tablet) {
    .event-name {
      @include font-data(16px, $title-text-color, 700, 1.25);
    }

    .event-local-date {
      @include font-data(16px, $subtitle-text-color, 400, 1.25);
    }

    .location-link {
      @include font-data(14px, $subtitle-text-color, 600, 1.2);
    }
  }
}

.event-name,
.event-local-date {
  margin-bottom: 5px;
}

