.tui-page-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 12px;
  margin-left: 5px;
  height: 30px;
  width: 30px;

  color: $subtitle-text-color;

  &:focus,
  &:hover {
    color: $title-text-color;
  }

  &.tui-is-selected {
    border-style: solid;
    border: 1px solid $title-text-color;
    color: $title-text-color;
    border-radius: 10px 0px;

    // & .pag-link {
    //   color: $title-text-color;
    // }
  }
}

.default-info {
  padding-top: 30px;
